function portfolioSlider() {
    var swiper = new Swiper('.portfolio-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: '.portfolio-slider .swiper-button-next',
            prevEl: '.portfolio-slider .swiper-button-prev',
        },
        pagination: {
            el: '.swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        on: {
            init: function () {
                renderCustomPagination(this)
            },
        },
    })

    swiper.on('slideChange', function () {
        renderCustomPagination(swiper)
    });

    function renderCustomPagination(swiper) {
        let slidesCount = document.querySelectorAll(".portfolio-slider .swiper-slide:not(.swiper-slide-duplicate)")
        let paginationCurrent = document.querySelector(".custom-pagination ._current")
        let paginationTotal = document.querySelector(".custom-pagination ._total")
        paginationCurrent.innerHTML = formatPaginationNum(swiper.realIndex+1)
        console.log(slidesCount.length)
        paginationTotal.innerHTML = formatPaginationNum(slidesCount.length)
    }

    function formatPaginationNum(num) {
        if(num < 10){
            return `0${num}`
        }
        return num
    }
}

function feedbackSlider() {
    var swiper = new Swiper('.feedback-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: '.feedback-slider .swiper-button-next',
            prevEl: '.feedback-slider .swiper-button-prev',
        },
    })
}

portfolioSlider()
feedbackSlider()
