let housePoints = document.querySelectorAll(".house-point")

housePoints.forEach(housePoint => {
    let btn = housePoint.querySelector(".house-point__btn")
    let closeBtns = housePoint.querySelectorAll("._close-point")

    btn.onclick = function () {
        housePoint.classList.toggle("_active")

        document.addEventListener( 'click', (e) => {
            const withinBoundaries = e.composedPath().includes(housePoint);

            if ( ! withinBoundaries ) {
                if(window.innerWidth <= 992){
                    if(housePoint.classList.contains("_active")){
                        housePoint.classList.remove("_active")
                    }
                }
            }
        })
    }

    closeBtns.forEach(close => {
        close.onclick = function () {
            housePoint.classList.remove("_active")
        }
    })

})
